import { Component, h } from '@stencil/core';
import { InfoTileProgress } from '@limetech/lime-elements';

/**
 * Using the primary slot
 */
@Component({
    tag: 'limel-example-info-tile-primary-slot',
    shadow: true,
    styleUrl: 'info-tile.scss',
})
export class InfoTilePrimarySlotExample {
    public render() {
        const link = {
            href: 'https://duckduckgo.com/?q=weather',
            title: 'Click to see real-time weather forecast',
            target: '_blank',
        };

        const progress: InfoTileProgress = {
            value: 76,
            prefix: '↑',
            displayPercentageColors: true,
        };

        return (
            <div>
                <limel-info-tile
                    icon="partly_cloudy_rain"
                    label="Partly cloudy with a risk of rain"
                    prefix="temp"
                    value="23"
                    suffix="°C"
                    link={link}
                    progress={progress}
                >
                    <span
                        slot="primary"
                        style={{
                            display: 'block',
                            background: 'red',
                        }}
                    />
                </limel-info-tile>
            </div>
        );
    }
}
